<template>
  <q-select
    :autofocus="autofocus"
    :dense="dense"
    outlined
    emit-value
    use-input
    option-value="id"
    hide-bottom-space
    class="rounded text-body2 input-white text-primary"
    popup-content-class="bg-white rounded-bottom"
    input-class="text-body1 letter-space-normal text-primary"
    :options="filteredOptions"
    v-model="selectModel"
    @filter="filterBrandsFn"
    @input-value="(val) => (inputModel = val)"
    :rules="[(val) => (required ? !!val || '' : true)]"
  >
    <template v-slot:option="scope">
      <q-item
        clickable
        v-bind="scope.itemProps"
        :class="scope.selected ? 'bg-accent text-secondary' : 'text-primary'"
        class="hover-text-secondary hover-bg-accent"
        @click="scope.opt?.action"
      >
        <q-item-section avatar v-if="scope.opt?.icon" style="min-width: unset">
          <q-icon size="xs" :name="scope.opt?.icon" />
        </q-item-section>
        <q-item-section>
          <q-item-label>
            {{ scope.opt?.title || scope.opt }}
          </q-item-label>
        </q-item-section>
      </q-item>
    </template>

    <template v-slot:selected>
      <div class="row items-center" v-if="!inputModel">
        <q-icon
          v-if="requestOptions[selectModel]?.icon"
          :name="requestOptions[selectModel]?.icon"
          size="sm"
        />
        <span :class="selectModel ? 'q-pl-sm' : 'text-placeholder'"
          >{{ requestOptions[selectModel]?.title || placeholder }}
        </span>
      </div>
    </template>
  </q-select>
</template>

<script>
import { defineComponent } from 'vue';
import { mapState, mapActions } from 'pinia';
import useGrowmodo from 'stores/growmodo';
import { searchInObjectArray } from 'src/assets/scripts/functions';

export default defineComponent({
  name: 'SelectTaskProjectType',

  props: {
    autofocus: {
      type: Boolean,
      default: false,
    },
    placeholder: {
      type: String,
      default: 'Select type',
    },
    initModel: {
      type: [String, Number],
      default: '',
    },
    dense: {
      type: Boolean,
      default: true,
    },
    type: {
      type: String,
      default: 'task',
    },
    required: {
      type: Boolean,
      default: false,
    },
  },

  watch: {
    initModel: {
      async handler(val) {
        if (val) this.selectModel = val;
      },
    },
  },

  data() {
    return {
      filteredOptions: [],
      selectModel: '',
      inputModel: '',
    };
  },

  async mounted() {
    if (this.initModel) this.selectModel = this.initModel;
    this.isTask
      ? await this.getTaskDirectory()
      : await this.getProjectDirectory();
  },

  computed: {
    ...mapState(useGrowmodo, ['taskDirectory', 'projectDirectory']),

    isTask() {
      return this.type.toLowerCase() === 'task' ? true : false;
    },

    requestOptions() {
      return this[this.isTask ? 'taskDirectory' : 'projectDirectory'];
    },

    listOptions() {
      return Object.values(this.requestOptions);
    },
  },

  methods: {
    ...mapActions(useGrowmodo, ['getTaskDirectory', 'getProjectDirectory']),

    filterBrandsFn(val, update) {
      update(() => {
        if (val === '') {
          this.filteredOptions = this.listOptions;
        } else {
          const query_result = searchInObjectArray(
            this.filteredOptions.slice(0, -1),
            val,
            ['title']
          );
          this.filteredOptions = query_result;
        }
      });
    },
  },
});
</script>
